<template>
	<view class="content">
		<customHeader :title="pageName" :showBack="true" :headHeight="headerHeight"></customHeader>
		<view class="cont" :style="{marginTop:marginTop+'px'}">
			<view class="box" :style="{top:marginTop+tabMargin+'px',background:list.length==0?'#fff':'#F3F3F5'}">
				<scroll-view  scroll-y="true" class="scroll-Y" :style="{height:scrollHeight+'px'}"  refresher-enabled="true" :refresher-triggered="refreshStatus" @refresherpulling="refreshData"
					@scrolltolower="lower">
					<view class="item" v-for="(item,index) in list" :key="index" @click="toDetail(item.id)">
						<view class="top flex flex_btween">
							<view class="title">合同编号：NO.{{item.code}}</view>
							<image :src="imgUrl+'/worker/ic_arrow_right.png'" mode=""></image>
						</view>
						<view class="middle">
							<view class="line flex flex-start">
								<view class="tit">工作名称：</view>
								<view class="text">{{item.project_name}}</view>
							</view>
							<view class="line flex flex-start">
								<view class="tit">开始时间：</view>
								<view class="text">{{item.signed_time}}</view>
							</view>
							<view class="line flex flex-start">
								<view class="tit">结束时间：</view>
								<view class="text">{{item.work_end_time}}</view>
							</view>
						</view>
					</view>
					<view class="empty" v-if="list.length == 0">
						<image :src="imgUrl+'/worker/empty_bg.png'" mode="heightFix"></image>
					</view>
				</scroll-view>
			</view>
		</view>
	</view>
</template>

<script>
	import customHeader from "@/components/custom_header.vue"
	const app = getApp()
	export default {
		data() {
			return {
				imgUrl:app.globalData.baseImageUrl,
				pageName:"我的合同",
				marginTop:app.globalData.marginTop,
				tabMargin:app.globalData.tabMargin,
				btnBottom:app.globalData.btnBottom,
				list:[],
				scrollHeight:0,
				boxTop:0,
				headerHeight:app.globalData.topHeight,
				currentPage:1,
				refreshStatus:true,
				isPulling:false,
			}
		},
		components:{
			customHeader
		},
		// onPullDownRefresh() {
		// 	this.currentPage = 1
		// 	this.list = []
		// 	this.getList()
		// },
		onLoad() {
			this.scrollHeight = app.globalData.systemHeight - this.marginTop - this.tabMargin - this.btnBottom
			this.getList()
		},
		methods: {
			refreshData(){
				let _this = this
				if(!this.isPulling){
					this.isPulling = true
					this.refreshStatus = true
					this.currentPage = 1
					this.list = []
					this.getList()
					setTimeout(function() {
						_this.refreshStatus = false
						_this.isPulling = false
					}, 1000);
				}
			},
			toDetail(id){
				let url = "/worker/contract/"+id+"/detail-url"
				this.$request(url).then(res => {
					if(res.code == 0){
						uni.navigateTo({
							url:"/pages/webview/webview?url="+encodeURIComponent(res.data.contract_url)
						})
					}else{
						uni.showModal({
							title:res.msg
						})
					}
				})
			},
			getList(){
				let url = "/worker/contracts?page="+this.currentPage
				this.$request(url).then(res => {
					if(res.code == 0){
						this.list = this.list.concat(res.data.list)
						this.pagination = res.data.pagination
					}else{
						uni.showModal({
							title:res.msg
						})
					}
				})
			},
			lower(){
				if(this.list.length < this.pagination.totalCount){
					this.currentPage++
					this.getList()
				}else{
					if(this.list.length >= this.pagination.totalCount){
						uni.showToast({
							title:"已经到底啦~",
							icon:"none",
							duration:2000
						})
					}
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
.box{
	position: fixed;
	width: calc(100% - 80rpx);
	left:40rpx;
	z-index:3;
	border-top-left-radius: 16rpx;
	border-top-right-radius: 16rpx;
	overflow: hidden;
	background: #F3F3F5;
	.item{
		padding:0 40rpx 40rpx 40rpx;
		border-radius: 16rpx;
		background: #fff;
		box-shadow: 0 2rpx 6rpx 0 rgba(0, 0, 0, 0.15);
		margin-bottom: 20rpx;
		.top{
			padding:40rpx 0;
			border-bottom: 1px dashed #DFDFDF;
			.title{
				font-size: 36rpx;
				position: relative;
				font-weight: bold;
				white-space: nowrap;
				text-overflow: ellipsis;
				overflow: hidden;
				width:calc(100% - 30rpx);
			}
			image{
				width: 30rpx;
				height: 30rpx;
			}
		}
		.middle{
			margin-top:28rpx;
		}
		.line{
			margin-top: 16rpx;
			font-size: 28rpx;
			.tit{
				width: 160rpx;
				color: #686868;
			}
			.text{
				width: calc(100% - 160rpx);
				color: #333333;
				white-space: nowrap;
				text-overflow: ellipsis;
				overflow: hidden;
			}
		}
		
	}
}
</style>
